<template>
  <v-app>
    <!-- Toolbar -->
    <v-toolbar height="35px" dense class="grey mb-0">
      <v-container class="ma-0 pa-0 mb-0">
        <v-layout column align-center>
          <v-flex xs3>
          </v-flex>
          <v-flex xs3>
      <v-btn small icon class="blue white--text" @click="showForm = !showForm">
        <v-icon> add </v-icon>
      </v-btn>
      <v-btn small icon class="orange white--text" @click="decrementTurn()">
        <v-icon>fast_rewind</v-icon>
      </v-btn>
      <v-btn small icon class="green white--text" @click="incrementTurn()">
        <v-icon>fast_forward</v-icon>
      </v-btn>
          </v-flex>
          <v-flex xs3>
          </v-flex>
        </v-layout>
      </v-container>
    </v-toolbar>

    <!-- Up Now and On Deck -->
    <lineup :full-list="fullList" :turn="turn">
    </lineup>

    <!-- New combatant form -->
    <add-form :show-form="showForm" @clearForm="clearCombatantForm()" @addCombatant="addCombatant" ></add-form>
    

    <!-- Combatant List -->
    <combatant-list :full-list="fullList" :turn="turn" @sort="sortList()" @removeCombatant="removeCombatant">
    </combatant-list>

   <v-card class="blue lighten-1 pa-3 white--text ma-2" v-if="fullList.length < 1" >
     <v-layout>
       <h1> Please Add a Combatant Using the + button </h1>
     </v-layout>
   </v-card>
        
  </v-app>
</template>

<script>
import Lineup from './components/Lineup.vue'
import AddForm from './components/AddForm.vue'
import CombatantList from './components/CombatantList.vue'

export default {
  data: () => {
    return {
      name: 'app',
      fullList: [],
      showForm: false,
      turn: 0,
      needsSort: true,
    }
  },

  watch: {
    turn() {
      
    },

    fullList() {
      this.savePlayers()
    }
  },

  components: {
    Lineup,
    AddForm,
    CombatantList
  },

  methods: {
    calculateToolbarClass(combatant) {
      if (this.turn == this.fullList.indexOf(combatant)) {
        return true
      } else {
        return false
      }
    },

    incrementTurn() {
      if (this.turn == this.fullList.length - 1) {
        this.turn = 0
      } else {
        this.turn++
      }
    },

    decrementTurn() {
      if (this.turn == 0) {
        this.turn = this.fullList.length - 1
      } else {
        this.turn--
      }
    },

    sortList() {
      this.fullList.sort(((a, b) => {
        return b.int - a.int
      }))
      this.needsSort = false
    },

    setupSort() {
      if (this.needsSort) {
        this.sortList()
        this.needsSort = false
      }
    },

    addCombatant(newbie) {
      this.fullList.push(newbie)
      this.sortList()
    },

    clearCombatantForm() {
      this.showForm = false
    },

    removeCombatant(combatant) {
      this.fullList.splice(this.fullList.indexOf(combatant), 1)
      this.sortList()
    },

    getNext() {
      if (this.turn == this.fullList.length - 1) {
        return this.fullList[0].name
      } else {
        return this.fullList[this.turn + 1].name
      }
    },

    savePlayers() {
      let playersToSave = []
      this.fullList.forEach(combatant => {
        if (combatant.isPlayer) {
          playersToSave.push(combatant)
        }
      })
      window.localStorage.setItem('InitTrackerSave', JSON.stringify(playersToSave))
    }
  },

  created() {
    if (window.localStorage.getItem('InitTrackerSave')) {
      this.fullList = JSON.parse(window.localStorage.getItem('InitTrackerSave'))
    }
  },

  mounted() {
    this.sortList()
  }
}
</script>

<style>
.v-label--active {
  font-size: 12px;
}
</style>
